import React from 'react';
import { VariantsDetails, VariantChipContainer } from './general/style/style';
import { DhProperties } from '../DhProperties';


export default function VariantChip({data, isFallbackCase}) {

  const onClickOnVariant = () => {
    try {
      const redirectUrl = typeof (data?.redirectUrl) === "string" ? data?.redirectUrl?.replace("<lang>", lang || "en") : null;
      const title = data?.redirectPageTitle || data?.title || "";
      if(typeof(newsHuntAction)!='undefined' && typeof(callEvent)!="undefined" && ((!isIos() && checkMinAndroidAppVersion(NH_BROWSER_SUPPORTED_VERSION)) || (isIos() && checkMinAndroidAppVersion(NH_BROWSER_SUPPORTED_VERSION_IOS))) && typeof(redirectUrl)==="string"){  
          const url= DhProperties.properties.pwaBaseUrl+"/webItem?webModel="+encodeURIComponent(JSON.stringify({title:title,url:DhProperties.properties.baseUrl+redirectUrl}));
          callEvent(url);
      } else if(typeof(redirectUrl)==="string"){ 
        window.location.href = DhProperties.properties.baseUrl+redirectUrl;
      }
    }catch(err){
      console.log("VariantChip: ",err);
    }
  }

  return (
    <VariantChipContainer isFallbackCase={isFallbackCase} active={data?.active} activeColor={data?.activeColor}>
      <VariantsDetails onClick={()=> !data.active && onClickOnVariant()}>
        <tr>
          {data && data.label && <th>{data.label}</th>}
        </tr>
        <tr>
          {data && data.price &&<td>â‚¹{data.price}</td>}
        </tr>
      </VariantsDetails>
    </VariantChipContainer>
  )
}

